	export default {
		data(){
			return {
				key: '', // 筛选条件
				isgo: false,
				isgotext: '确认核销',
				allData: [] // 提货单列表
			}
		},
		onLoad(e){
			if(e.id){
				this.key = e.id;
			}
			this.getLadingInfo();
		},
		computed: {
			// 获取选中的提货单id
			checkedIds () {
				let ids = []
				this.allData.forEach(item => {
					// 判断不是禁用状态 并且是选中状态 并且是未核销状态
					if (!item.disabled && item.checked && item.status === 1) {
						ids.push(item.id)
					}
				})
				return ids
			},
		},
		methods: {
			// 多选框点击事件处理
			checkboxChange (e) {
                var values = e.detail.value;
				this.allData.forEach(item => {
					if (values.includes(item.id)) {
						item.checked = true
					} else {
						item.checked = false
					}
				})
            },
			//获取提货单详情
			getLadingInfo() {
				if(this.key){
					let data = {
						'key': this.key
					}
					this.$api.ladingInfo(data, e => {
						if (e.status) {
							this.allData = this.formatData(e.data);
						} else {
							this.allData = []; // 清空数据
							this.$common.modelShow('提示', e.msg, function(){});
						}
					});
				}
			},

			//搜索
			search() {
				if(this.key != ''){
					this.getLadingInfo();
				}else{
					this.$common.errorToShow('请输入查询关键字');
					return false;
				}
			},
	
			//查询判断是否可以核销
			isGoWrite(data) {
				let isgo = false;
				if (data.order_info.pay_status == 2 && data.order_info.ship_status == 3){
					isgo = true;
					this.lading_id = data.id;
					this.goodsList = data.goods;
					this.allData = data;
				} else {
					this.$common.modelShow('无法核销', '订单必须支付并已发货才可以核销', function(){});
				}
				this.isgo = isgo;
			},
			// 数据转化
			formatData (data){
				data.forEach (item => {
					if (item.status === 2) {
						// 已提货
						this.$set(item, 'checked', false)
						this.$set(item, 'disabled', true)
					} else {
						// 未提货
						this.$set(item, 'checked', true)
						this.$set(item, 'disabled', false)
					}
				})
				return data
			},
			//去核销
			write() {
				let _this = this;
				this.$common.modelShow('提示', '您确认核销吗？', function(res){
					//去核销
					let data = {
						lading_ids: _this.checkedIds.join()
					}
					_this.$api.ladingExec(data, res => {
						if(res.status) {
							_this.$common.successToShow(res.msg, _this.afterChangeDataStatus())
						}
					});
				});
			},
			// 核销完成后更改数据状态
			afterChangeDataStatus () {
				this.allData.forEach(item => {
					if (this.checkedIds.indexOf(item.id) > -1) {
						item.status = 2;
						item.checked = false;
						item.disabled = true;
						this.getLadingInfo();
					}
				})
			}
		}
	}
